<template>
  <div class="h-1">hello world！</div>
  <p>
    在编程界呢有一个习俗，就是学习一门语言之前，要用这门语言来表达自己的博大胸襟和宽广情怀，因为大家都是心怀世界的有志之士，所以一般用“hello World”。现在就我就教教你怎么做。
  </p>
  <p>
    首先要清楚的是在哪里可以使用JavaScript，最常见的在HTML之中，在HTML几乎任何位置都可以使用JavaScript，你需要添加{{scriptTag}}{{scriptTagEnd}}标签，然后将代码放入其中。如果代码很长，你可以写到一个js文件里面，然后把js文件路径写到标签的src之中，听起来很难？别慌，很简单的。
  </p>
  <code-editor lang="html" v-model="codes[0] ">在html中使用JavaScript</code-editor>
  <!-- eslint-disable-next-line -->
  <p>接下来我就教你一句代码：<div class="no-br" data-help="console.log(message)|在终端输出message(内容),这里是输出hello World!"><div class="no-br" data-help="控制台 console|用于在控制台（在开发者工具中可见，按F12可以在console中找到输出内容）以各种形式输出内容进行调试，一般用于开发之中">console</div>.<div class="no-br" data-help="console.log(message)|在终端输出message(内容)">log</div>("hello,World!")</div><copy-button v-model="codes[1]"/></p>
  <p>然后将这句代码复制到下面的代码编辑器中运行，看看有什么效果吧。</p>
  <code-editor v-model="codes[2]">我的第一行JavaScript代码</code-editor>
  <p>尝试好了吗，我知道你肯定满头雾水，别急，我来一点一点带你走入JavaScript的世界。</p>
  
</template>

<script setup>
import { useUrlSearchParams } from "@vueuse/core";
import { defineAsyncComponent, reactive } from "vue";
import copyButton from './copyButton.vue';
useUrlSearchParams('hash').index=1;
const codeEditor = defineAsyncComponent(()=>import("./codeEditor.vue"));

const scriptTag='<scr' +'ipt>',scriptTagEnd='</scr'+'ipt>'
const scriptTagWithSrc = '<script src='
const codes = reactive([]);
codes[0] = `<html><body>${scriptTag}//JavaScript代码${scriptTagEnd}${scriptTagWithSrc}"xxx.js">//或者在xxx.js中写入JavaScript代码${scriptTagEnd}</body></html>`;
