<template>
  <div>
    <p class="h-1">变量与常量 · variate and constant</p>
    <p>经过了前几章的学习，相信冰雪聪明的小可爱一定学到了不少东西，并且产生一些疑问，譬如：函数里面的内容只能放那一个嘛，不能整的动态一些吗？</p>
    <p>很好，既然大家这么热情，我就向大家隆重介绍：常量和变量，这两位在之后将时常露面，成为我们不可或缺的好伙伴</p>
    <p class="h-3">声明和赋值</p>
    <p>声明（也可以称之为定义）是指告诉JavaScript引擎创建一个内存区域用于储存数据，并将这块区域命名，赋值则是向指定的内存区域写入值，无论是变量还是常量，都需要先声明，再赋值。（看不懂？等一会给你举个例子）</p>
    <p>常量：常量使用const关键词声明，必须在声明的同时赋值，格式为<code>const 常量名 = 值;</code></p>
    <p>变量：变量使用let关键字声明，可以在声明时赋值，格式为<code>let 变量名 = 值;</code></p>
    <el-alert type="warning" style="margin-bottom:15px">如果你接触过JavaScript，你可能知道var关键字，关于let和var区别将在后面详细讲解，没有特殊情况，请使用let</el-alert>
    <code-editor v-model="codes[0]">常量与变量的声明和赋值</code-editor>
      <!-- eslint-disable-next-line -->
    <p>常量和变量在同一<div class="no-br" data-help="作用域|即常量、变量、函数等可用的区域，在相应的作用域内，这些内容才可以被调用">作用域</div>中都只能声明一次，在声明后再次声明会报错</p>
    <code-editor v-model="codes[1]">常量与变量的重复声明</code-editor>
    <p>可以使用一个关键字为多个常量或变量声明并赋值</p>
    <code-editor v-model="codes[2]">同时声明多个变量或常量</code-editor>
    <p class="h-3">对于常量与变量的形象解释</p>
    <p>啊哈哈，例子来咯！</p>
    <p>变量与常量就像卡片一样，不过一个用铅笔写，一个用中性笔写（不可擦的那种）</p>
    <div>
    <el-card style="width:150px;margin-right:15px;float:left" body-style="padding:13px">
        <template #header><el-tooltip content="行不更名，坐不改姓">变量名</el-tooltip></template>
        <el-tooltip content="我是可改的"><el-input v-model="varValue"></el-input></el-tooltip>
    </el-card>
    <el-card style="width:150px;">
        <template #header><el-tooltip content="行不更名，坐不改姓">常量名</el-tooltip></template>
        <el-tooltip content="改不了，没这个能力知道吧"><div>2</div></el-tooltip>
    </el-card>
    </div>
    <p>声明就相当于拿出一张卡片，写上变量或常量的名字；赋值相当于在相应的卡片上写上内容，如果是用铅笔写的（变量）那么可以修改这个值，如果是中性笔写的（常量）就不能再改动了（不要试图划了重写）</p>
    <p>变量不管怎么赋值，它指向的都是同一块内存区域，就像卡片上的内容再改，它还是同一张卡片。</p>
    <p>而读取变量的值，就相当于看着卡片的名称读卡片上的内容，没错，就这么简单。</p>
    <p class="h-3">变量和常量的命名</p>
    <p>如果你需要声明有一个常量和一个变量，你会给它们取什么名字呢？</p>
    <code-editor v-model="codes[3]">错误示范</code-editor>
    <p>还有一些命名，符合条件，但是不建议使用</p>
    <code-editor v-model="codes[4]">不建议使用的命名</code-editor>
    <el-alert style="margin-top:15px" type="warning" title="不建议使用中文作为变量和函数名的原因">
        <p>1、编程是一件国际化很高的事情，使用英文可以让你的代码更容易被看懂（尤其是开源项目）</p>
        <p>2、中文在Unicode编码处理不当的情况下可能出现问题（锟斤拷锟斤拷...）导致无法运行</p>
        <p>3、在没有安装中文字体的设备中，口口口口口口</p>
        <p>4、在没有中文输入法的设备上，无法编辑</p>
        <p>5、一直换输入法是很不爽的事情（亲身经历）</p>
    </el-alert>
    <p>推荐的变量命名法：</p>
    <el-card style="margin-bottom:15px" header="驼峰命名法" title="驼峰命名法">
        <p>小驼峰法：即第一个单词以小写字母开始；从第二个单词开始以后的每个单词的首字母都采用大写字母，一般用于命名变量，比如：goodBoy,dearShock</p>
        <p>大驼峰法（帕斯卡命名法）：将每个单词的首字母都大写，包括第一个，一般用于命名构造函数和类名，比如BufferBuilder,DataLoader</p>
        <code-editor v-model="codes[5]">示例</code-editor>
    </el-card>
    <el-card header="下划线命名法" title="下划线命名法">
        <p>将字母与字母之间用下划线"_"连接，字母全部小写，比如my_dog,my_age</p>
        <code-editor v-model="codes[6]">示例</code-editor>
    </el-card>
    <p>推荐的常量命名法：</p>
    <el-card header="常量命名法" title="常量命名法">
        <p>常量命名分为两种情况：</p>
        <p>1、当常量值不确定，但是确定后不会修改时，使用小驼峰法，比如userName（用户名称，当没有获取到时未知，但是获取到之后不会变化（没有改名卡！））</p>
        <p>2、当常量值确定时，使用全大写命名，单词之间用下划线隔开，这种情况一般用于储存以后要用的数据，比如COLOR（颜色，用于储存16进制颜色代码）</p>
        <code-editor v-model="codes[7]" :innerData="innerData">示例</code-editor>
    </el-card>
    <p class="h-3">推荐的命名规则：</p>
    <p>1、变量名应该清晰，明显，可以一眼看出其含义</p>
    <p>2、变量名应尽量简洁，不要刻意使用长单词，比如userName不要写成subscriberName或nameOfUser</p>
    <p>3、与第一条对应，不要使用含义模糊不清的单词，比如value，data（几乎所有东西都有值和数据），除非这个变量在作用域内可以明显的知道其含义，这个以后会讲解。</p>
    <p>4、对于含义相同的内容，统一使用相同的名称，比如使用user代指用户，就不要再用visitor之类的，统一使用user，比如：userName，userId</p>
  </div>
</template>

<script setup>
import { useUrlSearchParams } from "@vueuse/core";
import { ElCard,ElAlert,ElTooltip,ElInput } from "element-plus";
import {  reactive, ref } from "vue";

const params = useUrlSearchParams("hash");
params.index = 3;
import codeEditor from './codeEditor.vue'
const varValue = ref(1)
const codes =reactive([])
codes[0]=`//常量的声明（常量声明时必须赋值）
const A = 1; //在要声明的常量名前加关键词 const（全小写，JavaScript大小写敏感），然后使用等号= 赋值
//变量的声明
let b = 2; //在要声明的变量前加关键词 let，然后用等号赋值
//变量先声明，再赋值
let c;
c=3 //与常量不同，变量可以先声明，再赋值，赋值之前的值为undefined（未定义）`
codes[1]=`//常量的重复声明
const A = 1;
const A = 2;
//变量的重复声明
// let b = 1;
// let b = 2;`
codes[2]=`//单行同时声明
const A=1,B=2,C=3; //可以在一行，但是可读性较低
let a=1,b,c=3
//多行同时声明
const D=4,
E=5,
F=6; //记得加分号，否则下一行内容会被认为是声明常量
let d=4
,e //也可以将逗号写到前面
,f=6; 
b=2,e=5;//变量赋值也可以缩写到一行
console.log("变量：",a,b,c,d,e,f);
console.log("常量：",A,B,C,D,E,F);
//这些写法都是合法的，只与个人审美有关`
codes[3]=`const 1st = "shock" //不能以数字开头;
let No.1 = "shock" //符号只能包含"$","_"，不能包含"." , "-" , "@" , "#" ,"%", "^" ,"&" , "*" , "?"等西文符号以及"——","《","》"，"？"等中文符号。`
codes[4]=`const 什么是 = "What is"; //没错你可以使用中文命名变量和常量，函数也可以
let 君の名は="Your Name"; //甚至可以用稻妻语
let อิ_อิ = "?" //甚至颜文字
console.log(什么是,君の名は,อิ_อิ); //console.log()接受多个参数时可以同时输出
//但是这种命名可能会让编辑器反感（或许接手你代码的同志会更反感）`
codes[5]=`//变量使用小驼峰法命名
let myDogName = "黄豆",myAge=18,myDog,me
//构造函数
function Man(age){
    this.age=age
}
//类名
class Dog{
    constructor(name){
        this.dogName = name
    }
}
me = new Man(myAge);
myDog = new Dog(myDogName);
console.log(me,myDog)
//不用在意内容，以后都会讲的`
codes[6]=`let my_name = "shock";
console.log(my_name);`
codes[7]=`const userName = getName();//在获取到名字前不确定其值，但是获取到之后不会修改，因此使用小驼峰命名法定义常量。
const BACKGROUD_COLOR = "#6272a4";//背景颜色确定，使用全大写和下划线命名
console.log(userName);
changeBgColor(BACKGROUD_COLOR);`

function changeBgColor(color){
    document.documentElement.style.setProperty("--el-bg-color",color);
    setTimeout(() => {
        document.documentElement.style.setProperty("--el-bg-color","#141414");
    }, 5000);
}
const innerData =reactive({names:['getName','changeBgColor'],func:[()=>"王小美",changeBgColor]})
</script>

<style lang="scss" scoped></style>
