import { createApp } from 'vue'
import App from './App.vue'
import { createRouter, createWebHashHistory } from 'vue-router'
// import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import 'element-plus/theme-chalk/dark/css-vars.css'
import helloWorld from '@/components/helloWorld.vue';
import varAndConst from '@/components/varAndConst.vue';
import beforeEverything from './components/beforeEverything.vue';
import apiAndStructure from './components/apiAndStructure.vue';
import { useStorage } from '@vueuse/core'
var routes = [{ path: "/", component: beforeEverything },
{ path: "/helloWorld", component: helloWorld },
{ path: '/varAndConst', component: varAndConst },
{ path: '/apiAndStructure', component: apiAndStructure }]
const router = createRouter({
    history: createWebHashHistory(),
    routes,
})
createApp(App).use(router).mount('#app')
const last_url = useStorage("last-url");
setTimeout(() => {
    router.replace(last_url.value)
}, 500);
window.router = router
router.beforeEach((to, before) => {
    if (!(before.fullPath == "/" && before.fullPath == to.path)) {
        last_url.value = to.path
        window.activePage && (window.activePage.value = to.path)
    }
})
window.resizeCallback = [function () {
    document.documentElement.style.setProperty(
        "--page-height",
        `${innerHeight}px`
    );
    document.documentElement.style.setProperty("--page-width", `${innerWidth}px`);
}]
onresize = () => {
    window.resizeCallback.forEach(callback => {
        callback()
    });
}
onresize();
document.documentElement.className = "dark"