<template>
  <el-card>
    <template #header>
      {{ url }}<slot></slot>
      <el-button
        :data-help="`${
          props.lang == 'html'
            ? '新开窗口预览|在新窗口中运行这个html查看效果'
            : '运行代码|运行这段JavaScript代码'
        }`"
        @click="run()"
        circle
        style="float: right"
        :icon="VideoPlay"
      ></el-button>
    </template>
    <div :style="`height:${height}px`" ref="editorElement"></div>
    <el-card
      v-show="showResult"
      :body-style="{ padding: '0px', paddingLeft: '35px' }"
      ><ul ref="result"
    /></el-card>
  </el-card>
</template>

<script setup>
import { VideoPlay } from "@element-plus/icons-vue";
//eslint-disable-next-line
import { onMounted, ref } from "vue";
import * as console_html from "console-log-html";
import { useObjectUrl } from "@vueuse/core";
import { ElCard, ElButton } from "element-plus";

//eslint-disable-next-line
const props = defineProps(["modelValue", "lang", "innerData"]);
//eslint-disable-next-line
const emit = defineEmits(["update:modelValue"]);
const editorElement = ref();
let editor;
const result = ref();
const showResult = ref(false);
const height = ref();
var url;
const monaco = window.monaco;
window.editor = editor;
function run() {
  if (props.lang == "html") {
    url = useObjectUrl(
      new Blob([props.modelValue], { type: "text/html,charset=UTF-8" })
    );
    window.open(url.value);
  } else {
    if (!showResult.value) showResult.value = true;
    console_html.connect(result.value);
    try {
      if (props.innerData) {
        new Function(...props.innerData?.names, props.modelValue)(
          ...props.innerData?.func
        );
      } else {
        new Function(props.modelValue)();
      }
    } catch (e) {
      console.error(e);
    }
    console_html.disconnect();
  }
}

onMounted(() => {
  editor = monaco.editor.create(editorElement.value, {
    value: props.modelValue,
    language: props.lang ? props.lang : "javascript",
    theme: "vs-dark",
    scrollbar: false,
    scrollBeyondLastColumn: false,
    scrollBeyondLastLine: false,
    editorOptions: {
      automaticLayout: true, // 自动布局,
      autoIndent: true, //自动缩进
    },
  });
  height.value =
    editor.getModel().getLineCount() *
    editor.getOption(monaco.editor.EditorOption.lineHeight);
  var callback = () => {
    emit("update:modelValue", editor.getValue());
    height.value =
      (editor.getModel().getLineCount() + 1) *
      editor.getOption(monaco.editor.EditorOption.lineHeight);
    editor.layout();
    //   watch(() => editor, emit("update:modelValue"));
  };
  editor.onDidChangeModelContent(() => {
    callback();
  });
  setTimeout(() => {
    editor
      .getAction("editor.action.formatDocument")
      .run()
      .then(() => {
        emit("update:modelValue", editor.getValue());
        callback();
      });
  }, 500);
});
</script>

<style>
.text-danger {
  color: var(--el-color-danger);
}
.text-warning {
  color: var(--el-color-warning);
}
</style>
