<template>
  <div class="h-1">代码结构与api · Structure and api</div>
  <el-alert style="margin-top: 15px" type="warning"
    >温馨提示：这部分会挖很多坑，不要慌，这相当于是JavaScript的概览，很多东西都是概念上的，以后会接触到，千万别因为啥都看不懂就急（也不要因此怀疑我的水平（不是））。</el-alert
  >
  <p>
    古人云：温故而知新。不过我认为你应该没有看完上一章就扔一边等十天半个月再打开，不过“以旧焕新”也不失为一个好办法，所以我们以前一章学习的咒语作为引子，看看JavaScript到底长啥样，到底怎么用。
  </p>
  <el-card>
    <template #header>
      复习console.log() （按住ctrl键再用鼠标指向不同部分查看含义）
    </template>
    <div
      class="big no-br"
      data-help="console|按照数据类型划分，console属于对象（object），也可以称之为类（class），其中包含着各种控制台调试方法（也可以叫函数，function）"
    >
      console
    </div>
    <div
      class="big no-br"
      data-help=". 点号|西文句号，是读取元素内容的符号，在这里是指读取console中的内容（在这里是log方法）"
    >
      .
    </div>
    <div
      class="big no-br"
      data-help="console.log|按数据类型划分，console.log属于函数（function），后加括号（西文括号）表示执行该函数，括号内的内容是参数，之间用“,”（西文逗号）隔开"
    >
      log
    </div>
    <div
      class="big no-br"
      data-help="Function(arg1,arg2,...)|表示执行某个函数（在这里指console.log），括号里可以包含多个参数，这里的参数是字符串'hello World!'"
    >
      ("hello World!")
    </div>
  </el-card>
  <p>
    这时候有小可爱要问了，这咒语是怎么来的？为什么有console.log这个东西？谁告诉你这个东西的？我去哪里可以知道这些东西？
  </p>
  <p class="h-3">应用程序接口 · Application Programming Interface(API)</p>
  <p>
    这些对象、函数都是由开发者设计并暴露出来的接口，你只需要按照开发者的要求传入参数，就可以实现相应的功能。
  </p>
  <p>
    有点抽象？我们用比喻来解释，假如你在家准备点外卖，你只需要在平台上把想吃的菜选上，然后付钱，一会外卖就送到了你家，在这个过程中，你不需要知道菜是怎么炒出来的，不用操心小哥乘坐的交通工具，全程只需要告诉商家我要吃啥（还有付钱）就可以了，用api来类比，就相当于：
  </p>
  <el-card>
    <template #header>
      类比api （按住ctrl键再用鼠标指向不同部分查看含义）
    </template>
    <div
      data-help="美团（记得打钱）|类比数据类型，应该属于对象（object），在这里相当于暴露的一个类，用于消费"
      class="big no-br"
    >
      美团
    </div>
    <div
      class="big no-br"
      data-help=". 点号|西文句号，是读取元素内容的符号，这里指使用美团的某些功能"
    >
      .
    </div>
    <div
      data-help="点外卖|一种行为，这里类比为函数（function）传入需要的菜品后就可以送到家"
      class="big no-br"
    >
      点外卖
    </div>
    <div class="big no-br">
      ('黄焖
      <div data-help="只因|一种动物" style="margin: 0" class="big no-br">
        鸡
      </div>
      米饭'
      <div data-help=", 逗号|西文逗号，用于分离传递的参数" class="big no-br">
        ,
      </div>
      <div class="big no-br">"拍黄瓜"</div>
      )
    </div>
  </el-card>
  <p>
    这里的美团.点外卖(...)就相当于我们生活中的api，我们不需要知道是怎么实现的（当然，学习其实现方法也是受益匪浅的），只需要知道怎么用就好。
  </p>
  <p>那我们应该去哪里找这些api和它们的用法呢？</p>
  <p>
    这些就要找相应的文档，比如JavaScript，可以在MDN上找到详细的文档和演示：<el-link
      href="https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference"
      type="primary"
      >JavaScript 参考</el-link
    >
  </p>
  <p>
    如果你去看了的话，应该会觉得很头疼吧，这么多怎么可能记得住啊，别慌，我的建议是，在需要实现某些功能时再去找，成功之后尽量记住并应用到其他项目之中，这样慢慢就会积累不少了。
  </p>
  <p>那么现在，我给你一个api文档，试着调用一下吧</p>
  <el-card>
    <template #header> show 函数用法 </template>
    <p>show(title,content)</p>
    <p>用途：显示一个包含标题和内容的卡片，</p>
    <p>参数：</p>
    <p>1、title （string 字符串）：消息卡片的标题</p>
    <p>2、content （string 字符串）：消息卡片的内容</p>
    <p>返回值：无</p>
    <code-editor :innerData="innerData" v-model="codes[1]"
      >尝试调用show()</code-editor
    >
  </el-card>
  <p class="h-3">语句</p>
  <p>
    知道了api是什么之后，就会舒服很多，不用纠结这些奇怪函数是啥了，问就是api。那我们继续学习JavaScript代码结构。
  </p>
  <p>javaScript由语句组成：</p>
  <code-editor v-model="codes[0]">JavaScript语句</code-editor>
  <p>一个不完整的语句在分行之后，会自动与下列的语句合并，比如：</p>
  <code-editor v-model="codes[2]">不完整语句合并</code-editor>
  <p>
    但是正如上面所说，JavaScript在支持省略换行符和自动合并的情况下是会有意外情况的，比如：
  </p>
  <code-editor v-model="codes[3]">省略换行符的意外情况</code-editor>
  <p>
    尝试之后可以发现，删除分号之后，只有第一行可以正常执行，这是因为JavaScript引擎错把后面的方括号与前面合并了，尝试读取前一个函数的返回值而导致报错（关于函数的内容后面会讲）
  </p>
  <p class="h-3">注释</p>
  <code-editor v-model="codes[4]">JavaScript注释</code-editor>
  <p>
    单行注释可以嵌套，多行注释不能嵌套，因为开头会与嵌套的注释结尾合成一个注释，导致出现非法字符
  </p>
  <code-editor v-model="codes[5]">注释的嵌套</code-editor>
  <p>
    注释可以极大地提高代码可读性，从而提高代码质量，这对自己或者合作成员来说都是有益的（点名批评自己）
  </p>
</template>

<script setup>
import { useUrlSearchParams } from "@vueuse/core";
import { ElMessageBox,ElAlert,ElCard } from "element-plus";
import { defineAsyncComponent, reactive } from "vue";

const codeEditor = defineAsyncComponent(()=>import("./codeEditor.vue"));
useUrlSearchParams("hash").index = 2;

const codes = reactive([]);
const innerData = reactive({ names: ["show"], func: [show] });
codes[0] = `//JavaScript语句之间用“;”（西文分号）隔开
console.log("hello World!");console.log("hello World!");console.log("hello World!");
//你可以像上面一样写成一行，不过分到不同行会更有可读性（更容易看懂），比如：
console.log("hello World!");
console.log("hello World!");
console.log("hello World!");
//大部分情况下，如果已经分行，分号可以省略（不过不推荐，尽量要保留分号，尤其是新手，因为会有意外情况（以后会说））比如：
console.log("hello World!")
console.log("hello World!")
console.log("hello World!")`;
codes[1] = `//试试看，你一定可以的
`;
codes[2] = `//可以正常运行
console.log(1
+
2)`;
codes[3] = `console.log("正常运行"); //在保留这个分号的时候,可以正常运行
["删了分号再试试"].forEach((res)=>{console.log(res)})
`;
codes[4] = `//在任何语句前加两个斜杠（/）表示单行注释，在斜杠后的内容不会被运行
console.log("我被运行了") //会输出我被运行了  注释可以用来解释代码含义，提高代码可读性
//console.log("我不会被运行") //注释还可以用来禁用代码，如果不确定要不要保留，或者需要暂时停用一部分代码，注释是很好的选择（选择这行，然后按ctrl+/试试）
/* 多行注释
你看
我特别厉害
可以注释这么多行
厉害吧
*/`;
codes[5] = `//注释 //注释的注释 //注释的注释的注释//注释的注释的注释的注释
/* 多行注释 /* 禁止套娃 */  */`;
function show(title, content) {
  ElMessageBox.confirm(title, content, {
    showCancelButton: false,
    showConfirmButton: false,
  });
}
</script>

