<template>
  <div v-if="ctrlState && help.length != 0" :style="boxStyles">
    <div ref="card" :style="menuStyle">
      <el-card :header="help[0]" style="width: 100%; height: 100%">
        {{ help[1] }}
      </el-card>
    </div>
  </div>
  <div :style="pointStyles" />
</template>
<script setup>
import {ElCard} from 'element-plus';
import { computed, reactive, ref } from "vue";
import {
  set,
  useElementBounding,
  useElementByPoint,
  useEventListener,
  useKeyModifier,
  useMouse,
} from "@vueuse/core";
const { x, y } = useMouse({ type: "client" });
const { element } = useElementByPoint({ x, y });
const helpElement = ref(element.value);
const bounding = reactive(useElementBounding(helpElement));
const ctrlState = useKeyModifier("Control");
const card = ref();
const cardBounding = reactive(useElementBounding(card));
useEventListener("scroll", bounding.update, true);
const help = computed(() => {
  var getNearestHelp = function ($element) {
    if ($element == null) return [];
    if (!$element.dataset.help) {
      return getNearestHelp($element.parentElement);
    } else {
      set(helpElement, $element);
      return [...$element.dataset.help.split("|"), $element];
    }
  };
  return getNearestHelp(element.value);
});
const boxStyles = computed(() => {
  if (ctrlState.value && element.value && help.value) {
    return {
      display: "block",
      width: `${bounding.width + 5}px`,
      height: `${bounding.height + 5}px`,
      left: `${bounding.left - 2.5}px`,
      top: `${bounding.top - 2.5}px`,
      transition: "all 0.05s linear",
      position: "fixed",
      "z-index": 9999,
      border: "2px dashed var(--el-color-primary)",
      backgroundColor: "rgba(var(--el-color-primary-rgb), 0.2)",
      "border-radius": "0.2rem",
      "pointer-events": "none",
    };
  }
  return {
    display: "none",
  };
});

const menuStyle = computed(() => {
  return {
    width: `400px`,
    left: `${innerWidth - x.value < 422.5 ? innerWidth - 420 : x.value + 10}px`,
    top: `${
      innerHeight - y.value < cardBounding.height + 22.5
        ? innerHeight - cardBounding.height - 20
        : y.value + 10
    }px`,
    position: "fixed",
    "z-index": 9998,
  };
});

const pointStyles = computed(() => ({
  transform: `translate(calc(${x.value}px - 50%), calc(${y.value}px - 50%))`,
}));
</script>
